.App {
  width: 100vw;
}

h1{
  font-family: Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif;
  text-align: center;
  font-size: 4rem;
  margin: 50px;
}

.content {
  width: 500px;
  margin: auto;
}

.content > * {
  width: 100%;
  margin: 10px;
}


.row{
  display: flex;
}

.row > *{
  flex: 1;
}

.pixelsgrid {
  display: grid;
  grid-template-rows: repeat(auto-fill,1fr);
  height: 500px;
  background-color: #fff;
}

.cell {
  border: 0.1px solid #000;
  height: 100%;
  cursor: pointer;
}

.controls{
  display: flex;
  justify-content: space-between;
}

.btn{
  border: none;
  cursor: pointer;
  padding: 10px;
  background-color: brown;
  color: #fff;
  font-size: 18px;
  font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
  transition: background-color .3s;
}

.btn:hover{
  background-color: rgb(207, 76, 76);
}

.color{
  width: 30px;
  height: 30px;
  border: 1px solid #000;
  cursor: pointer;
}

.btn--download{
  display: flex;
  align-items: center;
  justify-content: center;
}


.btn--download img{
  width: 20px;
  fill: #fff;
}